@import url(https://fonts.googleapis.com/css?family=Noto+Sans);
/****************************/
/* Reset Style              */
/****************************/
body, p, h1, h2, h3, h4, h5, h6, ul, ol, li, dl, dt, dd, table, thead, tbody, tr, th, td, form, fieldset, legend, input, textarea, button {
  margin: 0;
  padding: 0;
  font-weight: normal; }

body, input, select, div, textarea, table, button {
  font-size: 1rem;
  font-family: 'Noto Sans','dotum', sans-serif;
  color: #222; }

table {
  width: 100%;
  border: 0;
  border-spacing: 0px;
  border-collapse: collapse; }

option {
  vertical-align: middle;
  padding: 3px 0; }

/* Firefox Fix */
legend {
  display: none;
  visibility: hidden; }

img, fieldset, button {
  vertical-align: middle;
  border: 0; }

ul, ol, li {
  list-style: none; }

em, address {
  font-style: normal; }

input, textarea, button {
  vertical-align: middle; }

/*input*/
input[type=text], input[type=password] {
  background: #ffffff;
  width: 126px;
  height: 17px;
  padding: 4px 0 1px 0;
  color: #454545;
  text-indent: 7px;
  vertical-align: middle;
  border: 1px solid #e0e0e0; }

textarea {
  background: #ffffff;
  width: 100%;
  height: 100px;
  padding: 4px 0 1px 0;
  color: #454545;
  vertical-align: middle;
  border: 1px solid #e0e0e0; }

input[type=text]:hover, input[type=password]:hover, textarea:hover {
  border: 1px solid #89b4d3; }

select {
  border: 1px solid #e0e0e0;
  height: 24px;
  padding: 3px 3px 3px 4px;
  vertical-align: middle;
  line-height: 24px; }

input[type="file"] {
  vertical-align: middle;
  height: 22px;
  width: 80%;
  margin-right: 6px; }

input[type="checkbox"], input[type="radio"] {
  margin: 0;
  padding: 0;
  vertical-align: middle;
  width: 13px;
  height: 13px;
  line-height: 13px; }

label {
  cursor: pointer; }

a {
  text-decoration: none;
  color: #454545; }

a:hover, a:active, a:focus {
  text-decoration: none; }

.hide {
  display: block;
  height: 0;
  width: 0;
  font-size: 0;
  line-height: 0;
  margin: 0;
  padding: 0;
  overflow: hidden; }

/****************************/
/* Color item               */
/****************************/
/****************************/
/* Mixin                    */
/****************************/
/****************************/
/* Common Style             */
/****************************/
* {
  box-sizing: border-box; }

body {
  font-size: 1rem; }

body, input, select, div, textarea, table, button {
  color: #222; }

input[type=text], input[type=password], label, button, select, .select_custom .default {
  color: #777; }

.alC {
  text-align: center !important; }

.ml_20 {
  margin-left: 20px; }

.hide_sc {
  overflow: hidden; }

/****************************/
/* heading                  */
/****************************/
h1, h2, h3, h4, h5, h6 {
  font-weight: 600; }

h1 {
  font-size: 2.8125rem; }

h2 {
  font-size: 2.25rem; }

h3 {
  font-size: 1.625rem; }

h4 {
  font-size: 1.25rem; }

h5 {
  font-size: 1.125rem; }

h6 {
  font-size: 1rem; }

/****************************/
/* Color item               */
/****************************/
/****************************/
/* Mixin                    */
/****************************/
/****************************/
/* Layout Style             */
/****************************/
#container {
  position: relative;
  max-width: 1280px;
  padding: 30px;
  margin: 0 auto; }

section {
  padding-top: 40px;
  margin-bottom: 60px;
  border-top: 1px solid #ccc; }

header h1.logo {
  font-size: 1rem; }

/****************************/
/* Color item               */
/****************************/
/****************************/
/* Mixin                    */
/****************************/
header {
  position: relative;
  height: 50px;
  margin-bottom: 20px; }
  header .logo {
    position: absolute;
    left: 50px; }
  header .menu {
    position: absolute;
    top: 0;
    left: 0;
    border: 1px solid #ccc;
    display: inline-block;
    width: 30px;
    height: 30px; }

nav {
  position: absolute;
  left: 0;
  top: 40px;
  display: none; }
  nav ul {
    font-size: 1rem; }
    nav ul:after {
      content: '';
      display: block;
      clear: both; }
    nav ul li {
      float: left;
      margin-right: 20px; }
      nav ul li a {
        text-transform: uppercase; }
        nav ul li a.active {
          font-weight: bold; }

/****************************/
/* grid                     */
/****************************/
.colbox {
  display: flex;
  flex-wrap: wrap; }
  .colbox > div {
    margin: 2rem; }
  .colbox.col2 > div {
    width: calc(50% - 2rem); }
    .colbox.col2 > div:nth-child(odd) {
      margin-left: 0; }
    .colbox.col2 > div:nth-child(even) {
      margin-right: 0; }
  .colbox.col3 > div {
    width: calc(33.333% - 2.68456rem); }
    .colbox.col3 > div:nth-child(3n-2) {
      margin-left: 0; }
    .colbox.col3 > div:nth-child(3n) {
      margin-right: 0; }
  .colbox.col4 > div {
    width: calc(25% - 3.00752rem); }
    .colbox.col4 > div:nth-child(4n-3) {
      margin-left: 0; }
    .colbox.col4 > div:nth-child(4n) {
      margin-right: 0; }
  .colbox.col5 > div {
    width: calc(20% - 3.2rem); }
    .colbox.col5 > div:nth-child(5n-4) {
      margin-left: 0; }
    .colbox.col5 > div:nth-child(5n) {
      margin-right: 0; }
  .colbox.col6 > div {
    width: calc(16.66% - 4rem); }
    .colbox.col6 > div:nth-child(6n-5) {
      margin-left: 0; }
    .colbox.col6 > div:nth-child(6n) {
      margin-right: 0; }
  .colbox > .colbox {
    margin-top: 0;
    margin-bottom: 0; }

/****************************/
/* Color item               */
/****************************/
/****************************/
/* Mixin                    */
/****************************/
/****************************/
/* media                    */
/****************************/
@media (max-width: 768px) {
  .colbox.col3 > div,
  .colbox.col4 > div,
  .colbox.col5 > div,
  .colbox.col6 > div {
    width: calc(50% - 2rem); }
  .colbox > div {
    margin: 2rem !important; }
    .colbox > div:nth-child(odd) {
      margin-left: 0 !important; }
    .colbox > div:nth-child(even) {
      margin-right: 0 !important; } }

@media (max-width: 480px) {
  html {
    font-size: 90%;
    transition: 0.3s; }
  .colbox.col2 > div,
  .colbox.col3 > div,
  .colbox.col4 > div,
  .colbox.col5 > div,
  .colbox.col6 > div {
    width: 100%;
    margin: 0 0 4rem 0 !important; } }

/****************************/
/* Color item               */
/****************************/
/****************************/
/* Mixin                    */
/****************************/
.item_wrap:after {
  content: '';
  display: block;
  clear: both; }

.item_card {
  position: relative;
  width: 300px;
  float: left; }
  .item_card a {
    display: block; }
    .item_card a strong {
      font-size: 1rem; }
    .item_card a .img {
      width: 100%;
      height: 57%;
      overflow: hidden;
      background-color: #eee; }
      .item_card a .img img {
        width: 100%;
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
        transition: 0.3s; }
    .item_card a .cont {
      margin-top: 20px; }
    .item_card a:hover img {
      -webkit-transform: scale(1);
              transform: scale(1); }

.item_view {
  position: fixed;
  width: 100% !important;
  height: 100%;
  padding: 30px;
  background: #fff;
  z-index: 999;
  top: 0;
  left: 0;
  overflow-y: auto; }
  .item_view .visual {
    margin-bottom: 20px; }
    .item_view .visual img {
      width: 100%; }
  .item_view .desc {
    margin-bottom: 20px; }
  .item_view .close {
    display: inline-block;
    position: absolute;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    top: 20px;
    right: 20px;
    background-color: rgba(0, 0, 0, 0.4); }
    .item_view .close:before, .item_view .close:after {
      content: '';
      display: inline-block;
      position: absolute;
      width: 50%;
      height: 2px;
      top: 50%;
      left: 0;
      right: 0;
      margin: auto;
      border-radius: 5px;
      background-color: #fff; }
    .item_view .close:before {
      -webkit-transform: rotate(45deg);
              transform: rotate(45deg); }
    .item_view .close:after {
      -webkit-transform: rotate(-45deg);
              transform: rotate(-45deg); }

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

