
@import 'style';

.item_wrap{
    @include clear; 
}

.item_card {
    position: relative;
    width: 300px;
    float:left;

    a{
        display:block;

        strong{
            font-size: 1rem;
        }
        .img{
            width:100%;
            height: 57%;
            overflow: hidden;
            background-color: #eee;
            img{
                width:100%;
                transform:scale(1.1);
                transition: $speed;
            }
        }
        .cont{
            margin-top: 20px;
        }

        &:hover{
            img{
                transform:scale(1);
            }
        }
    }
}

.item_view{
    position: fixed;
    width: 100% !important;
    height: 100%;
    padding: 30px;
    background: #fff;
    z-index: 999;
    top:0;
    left:0;
    overflow-y: auto;
    .visual{
        margin-bottom: 20px;
        img{
            width:100%;
        }
    }
    .desc{
        margin-bottom: 20px;
    }
    .close{
        display: inline-block;
        position: absolute;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        top: 20px;
        right: 20px;
        background-color: rgba(0,0,0,.4);
        &:before,
        &:after{
            content: '';
            display: inline-block;
            position: absolute;
            width: 50%;
            height: 2px;
            top: 50%;
            left: 0;
            right: 0;
            margin: auto;
            border-radius: 5px;
            background-color: #fff;
        }
        &:before{
            transform: rotate(45deg);
        }
        &:after{
            transform: rotate(-45deg);
        }
    }
}