
@import 'style';

header {
   position: relative;
   height: 50px;
   margin-bottom:20px;

   .logo {
        position: absolute;
        left: 50px;
   }

   .menu {
       position: absolute;
       top: 0;
       left: 0;
       border: 1px solid #ccc;
       display: inline-block;
       width: 30px;
       height: 30px;

   }
}

nav {
    position: absolute;
    left: 0;
    top: 40px;
    display: none;
    // transform-origin: 100% top;
    // transform: rotate(-90deg);
    ul {
        @include clear;
        font-size:1rem;
        li {
            float:left;
            margin-right:20px;
            a {
                text-transform:uppercase;
                &.active {
                    font-weight:bold;
                }
            }
        }
    }
}